@import url("https://fonts.googleapis.com/css2?family=Varela+Round&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 10px;
}

body {
  width: 100%;
  display: flex;
  justify-content: center;
  font-family: "Varela Round", sans-serif;
}

.wrapper {
  background-color: #eeeeee;
  border-radius: 0.8rem;
}
.wrapper:not(:first-child) {
  padding: clamp(1.6rem, 2.2224vw, 3.2rem);
}
.wrapper:not(:last-child) {
  margin-bottom: 3.2rem;
}

.all-wrapper {
  width: 74%;
  margin-bottom: 3.2rem;
}
.all-wrapper .main {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  padding: 3.2rem 0;
}
.all-wrapper .main .header-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  height: clamp(6.4rem, 13.1955000001vw, 19rem);
}
.all-wrapper .main .header-wrapper .header span {
  font-size: clamp(3rem, 6.9450000001vw, 10rem);
}
.all-wrapper .main .header-wrapper .header__bscs {
  color: #212121;
  margin-right: clamp(1.3rem, 2.5002vw, 3.6rem);
}
.all-wrapper .main .header-wrapper .header__forum {
  color: #f37348;
}
.all-wrapper .main .form-wrapper {
  flex: 1;
}
.all-wrapper .main .form-wrapper .form {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.all-wrapper .main .form-wrapper .form__comment {
  flex: 1;
  padding: clamp(1.3rem, 1.1112vw, 1.6rem);
  margin-bottom: 1.6rem;
  border: 0.3rem solid #dbdfe4;
  border-radius: 0.8rem;
  resize: none;
  font-family: "Varela Round", sans-serif;
  font-size: clamp(1.6rem, 1.389vw, 2rem);
}
.all-wrapper .main .form-wrapper .form__comment::placeholder {
  color: #687a86;
  font-family: "Varela Round", sans-serif;
  font-size: clamp(1.6rem, 1.389vw, 2rem);
}
.all-wrapper .main .form-wrapper .form__username {
  height: 4.5rem;
  margin-bottom: 1.6rem;
  padding: clamp(1.3rem, 1.1112vw, 1.6rem);
  border: 0.3rem solid #dbdfe4;
  border-radius: 0.8rem;
  font-family: "Varela Round", sans-serif;
  font-size: 1.6rem;
}
.all-wrapper .main .form-wrapper .form__username::placeholder {
  color: #aeb8be;
  font-family: "Varela Round", sans-serif;
  font-size: 1.6rem;
}
.all-wrapper .main .form-wrapper .form__post-button {
  height: 4.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  border-radius: 0.8rem;
  background-color: #f37348;
  font-family: "Varela Round", sans-serif;
  color: #fff;
  font-size: clamp(1.6rem, 1.389vw, 2rem);
}
.all-wrapper .comment-section-wrapper .comment-section__comment:not(:last-child) {
  margin-bottom: clamp(1.6rem, 1.389vw, 2rem);
}
.all-wrapper .comment-section-wrapper .comment-section__comment__info {
  margin-bottom: 0.5rem;
}
.all-wrapper .comment-section-wrapper .comment-section__comment__info__username {
  display: inline-block;
  font-size: 1.3rem;
  color: #f37348;
}
.all-wrapper .comment-section-wrapper .comment-section__comment__info__date {
  display: inline-block;
  font-size: 1.2rem;
  color: #687a86;
}
.all-wrapper .comment-section-wrapper .comment-section__comment__info__date span {
  font-size: 1.3rem;
  color: #aeb8be;
}
.all-wrapper .comment-section-wrapper .comment-section__comment__post {
  font-size: 1.5rem;
  color: #212121;
}